	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Rosenmontag","date":"06 Mar 2000","slug":"/2000-03-06-rosenmontag/","tags":"Y2000"},"html":"<p><a href=\"http://scriptingnews.userland.com/backissues/2000/03/05\">Scripting News</a>:  \"Cafe Boronne in Menlo Park is a European type hangout place smack in the middle of Silicon Valley.\" But the <a href=\"http://scriptingnews.userland.com/stories/storyReader$448\">picture</a> doesn't look like any hangout I've seen in Europe. </p>\n<p>I've become jaded. For me, a European cafe is a tiny <A HREF=http://www.tchibo.dTchibo coffee counter. You see them on every corner, and they all look alike. No place to sit... no chairs at all. Stay standing, drink your coffee in 5 minutes (no food served) and leave. </p>\n<p>(If you folow the link, you'll see Tchibo looks more like a fashion catalog than a coffee company. They make more profit at their coffee shops from clothes than from coffee, since the discount store <a href=\"http://www.aldi.de\">Aldi</a> has ruined the coffee market. Tchibo mentions coffee on their web page only as an afterthought.)</p>\n<p>Further arguments against the superiority of European culture</p>\n<ul>\n<li><a href=\"http://loveparade.techno.de/\">Love Parade</a>\n</li>\n<li><a href=\"http://www.bbc.co.uk/eurovision/\">Eurovision Song Contest</a>\n</li>\n</ul>"},"next":{"fields":{"title":"Saturday: He's baaaack!","date":"04 Mar 2000","slug":"/2000-03-04-saturday-hes-baaaack/"}},"previous":{"fields":{"title":"Super Tuesday","date":"07 Mar 2000","slug":"/2000-03-07-super-tuesday/"}}},{"node":{"fields":{"title":"Saturday: He's baaaack!","date":"04 Mar 2000","slug":"/2000-03-04-saturday-hes-baaaack/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/03/20000304010.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"crh0304.jpg: \" /></p>\n<p>Fortune: <a href=\"http://www.fortune.com/fortune/2000/03/06/ste.html\">Hard Times: A Letter from 2035</a> (via <a href=\"http://slashdot.org\">slashdot</a>)</p>\n<p>CNNSI <a href=\"http://cnnsi.com/soccer/world/news/2000/03/03/german_roundup_ap/\">Hamburg held to 4-4 draw</a> (they blew1 lead in Wolfsburg), but <a href=\"http://cnnsi.com/soccer/world/news/2000/03/04/german_roundup/\">Bayern falls in Matth채us' Bundesliga goodbye</a>. Lothar Matth채us is leaving Munich to play in the US for the MetroStars. He enjoys the sound of his own voice. Fans in New York should be glad that he can't speak English yet.</p>\n<p>Via <a href=\"http://cuwu.editthispage.com/\">curmudgeon</a>:  \"Are Europeans more mature than Americans? In an exchange between Kate Adams (\"brdbrain\") and Andrea Frick (\"andrea\"), Kate mentioned that Europeans are more mature, and Andrea thought that was strange.\"</p>\n<p>My first thought was that Europeans are forced to make more choices younger than Americans do. Mama, like all Germans, started to specialize her schooling already in the 5th or 6th grade. I didn't start to specialize until my 3rd year of college. <i>Of course, both our careers today are nothing like we could have imagined back then!</i></p>\n<p>The curmudgeon said: \" It is funny, but I, too, assume Europeans are more mature. Maybe it's because European kids are allowed more freedom as they grow up. Schools in the US spend a lot of effort on \"controlling\" students.\" <i>Funny, I said forced to choose, he said free to choose. Same difference?</i> </p>\n<p>Actually, I find the similarities between Germans and Americans much more interesting than the differences!</p>\n<p>As for our half-American, half-European son, he's just 10 weeks old, but he seems as mature as at least 12 weeks!</p>"},"next":{"fields":{"title":"Friday","date":"03 Mar 2000","slug":"/2000-03-03-friday/"}},"previous":{"fields":{"title":"Rosenmontag","date":"06 Mar 2000","slug":"/2000-03-06-rosenmontag/"}}},{"node":{"fields":{"title":"Friday","date":"03 Mar 2000","slug":"/2000-03-03-friday/","tags":"Y2000"},"html":"<p>Mama and Christopher come back home this afternoon. Until then, the best ETP baby pictures are at \"Familie Berg\". Today: <a href=\"http://familieberg.editthispage.com/\">Hartes Brot</a></p>"},"next":{"fields":{"title":"Thursday","date":"02 Mar 2000","slug":"/2000-03-02-thursday/"}},"previous":{"fields":{"title":"Saturday: He's baaaack!","date":"04 Mar 2000","slug":"/2000-03-04-saturday-hes-baaaack/"}}}],"pathPrefix":"","first":false,"last":false,"index":1074,"pageCount":1101}}

